nav{
    background: white;
    width:max-content;
    display:block;
    padding:0.7rem 1.7rem;
    z-index:2;
    position:fixed;
    left:50%;
    transform: translate(-50%);
    bottom: 2rem;
    display:flex;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding:0.9rem;
    border-radius: 50%;
    display:flex;
    color:blue;

}

nav a:hover{
    background: darkblue;
}

nav a.active{
    background:darkblue;
    color: var(--color-bg);
}